* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer-style {
  min-height: 100vh;
  background: linear-gradient(45deg, #1870ed 0, #f18f88 100%);
}

.timer {
  background-color: #080808;
  border-radius: 5px;
  margin-top: 30px;
  padding: 30px 20px;
  display: inline-flex;
  box-shadow: 1px 1px 5px #ffffff26, -4px 5px 8px 3px #00000040;
}

.field {
  flex-direction: column;
  align-items: center;
  margin-left: 20px;
  margin-right: 20px;
  display: flex;
  position: relative;
}

.field:last-child {
  margin-right: 0;
}

.field:first-child {
  margin-left: 0;
}

.field:not(:last-child):before, .field:not(:last-child):after {
  content: "";
  height: 5px;
  width: 5px;
  background-color: #ffffff4d;
  border-radius: 50%;
  display: block;
  position: absolute;
  right: -21px;
}

.field:before {
  top: 35%;
}

.field:after {
  top: 50%;
}

.value {
  color: #fff;
  text-transform: uppercase;
  font-size: 4.2rem;
}

.label {
  color: #ffffff59;
  text-transform: uppercase;
  margin-top: 10px;
  font-size: .7rem;
}

a {
  color: #253175;
}

